import React, { FC, useEffect, useState } from 'react';
import styled from 'styled-components';

import { CalculateItem, CalculateTossItem, UserItem } from '../../../../types';
import { SelectUser } from '../User';

interface Props {
  userList: UserItem[];
  calculateList: CalculateItem;
  allTossList: CalculateTossItem;
}
export const HostSend: FC<Props> = ({
  userList,
  calculateList,
  allTossList,
}) => {
  const [selectedUser, setSelectedUser] = useState('');

  useEffect(() => {
    if (userList.length) {
      if (!userList.find((item) => item.userName === selectedUser)) {
        setSelectedUser(userList[0].userName);
      }
    } else {
      setSelectedUser('');
    }
  }, [userList]);
  return (
    <StyledContainer>
      {Object.keys(allTossList).length > 0 ? (
        <>
          <SelectUser
            placeholder="담당자"
            userList={userList}
            value={selectedUser}
            onChange={(value) => setSelectedUser(value)}
          />
          <StyledTitle>
            한 번에 처리해볼까..?
            <StyledDesc>* 한 명이 다 송금받아서 정산한다면?</StyledDesc>
          </StyledTitle>
          <StyledGiveMe>
            <StyledTossUl>
              {Object.keys(calculateList).map((payer, index) => {
                const { paymentTotal, sendList } = calculateList[payer];
                const tossTotal = Object.keys(sendList).reduce(
                  (sum, key) => sum + Math.floor(sendList[key] || 0),
                  0,
                );
                return (
                  <StyledTossLi key={index}>
                    <StyledTossPayer>{payer} :</StyledTossPayer>
                    <StyledTossPrice>
                      {tossTotal.toLocaleString()}원
                    </StyledTossPrice>
                  </StyledTossLi>
                );
              })}
              <StyledTossLi>
                <StyledTossPayer>Total :</StyledTossPayer>
                <StyledTossPrice>
                  {allTossList['totalPrice'].toLocaleString()}원
                </StyledTossPrice>
              </StyledTossLi>
            </StyledTossUl>
          </StyledGiveMe>
          <div>
            <StyledTossUl>
              {Object.keys(allTossList).map((payer, index) =>
                payer !== 'totalPrice' && allTossList[payer] !== 0 ? (
                  <StyledTossLi key={index}>
                    {payer}(이)가
                    <StyledTotalPrice>
                      {allTossList[payer].toLocaleString()}
                    </StyledTotalPrice>
                    &nbsp;원을 요청해요!
                  </StyledTossLi>
                ) : null,
              )}
            </StyledTossUl>
          </div>
        </>
      ) : null}
    </StyledContainer>
  );
};
const StyledContainer = styled.div``;

const StyledGiveMe = styled.div`
  color: #222;
  border-top: 1px solid #dedede;
  font-weight: bold;
  padding: 10px 0px;
  ul {
    margin-top: 10px;
    margin-left: 15px;
  }
`;
const StyledTitle = styled.div`
  font-size: 22px;
  color: #222;
  font-weight: bold;
`;
const StyledDesc = styled.div`
  font-size: 13px;
  color: #646464;
  margin: 10px 0px;
`;
const StyledTossUl = styled.ul`
  padding-left: 20px;
`;
const StyledTossLi = styled.li`
  margin-bottom: 5px;
  font-size: 15px;
  list-style-type: disc;
`;
const StyledTotalPrice = styled.span`
  width: 80px;
  margin-left: 5px;
  display: inline-block;
  text-align: right;
`;
const StyledTossPayer = styled.span`
  display: inline-block;
  width: 50px;
`;
const StyledTossPrice = styled.span`
  display: inline-block;
  width: 100px;
  text-align: right;
`;
